  color: #f1c9e3;
}

a {
  color: #e17be1;
  text-shadow: 0 2px 10px #0003;
  opacity: .85;
}

a:visited {
  color: #c25ec2;
}

a:focus, a:hover {
  opacity: 1;
}

code {
  text-shadow: 0 0 2px #000;
  font-weight: 700;
}

